.noise_container{
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      overflow: hidden;
      pointer-events: none;
      z-index: 1;
      &:before{
        content: '';
        position: absolute;
        top: -100%;
        left: -100%;
        height: 300%;
        width: 300%;
        background: url(../../dist/images/noise.png);
        pointer-events: none;
        display: block;
        opacity: .4;
        animation: grain 2s steps(10) infinite;

      }
}

@-webkit-keyframes grain{
  0%,to{transform:translate(0);transform:translate(0)}
  10%{transform:translate(-5%,-10%);transform:translate(-5%,-10%)}
  20%{transform:translate(-15%,5%);transform:translate(-15%,5%)}
  30%{transform:translate(7%,-25%);transform:translate(7%,-25%)}
  40%{transform:translate(21%,25%);transform:translate(21%,25%)}
  50%{transform:translate(-25%,10%);transform:translate(-25%,10%)}
  60%{transform:translate(15%,5%);transform:translate(15%,5%)}
  70%{transform:translateY(15%);transform:translateY(15%)}
  80%{transform:translate(25%,35%);transform:translate(25%,35%)}
  90%{transform:translate(-10%,10%);transform:translate(-10%,10%)}
}




