//NAVIGATION

.menu_icon_container{
  position:fixed;
  right:1rem;
  top:1rem;
  z-index:11;
  width:60px;
  height:54px;
  background-color:rgba(15,22,38,0.05);
  cursor:pointer;
  overflow:hidden;
  @include bp(babybear){
    right:1rem;
  top:1rem;
  }

  &:after{
    content:'';
    background-color:$yellow;
    position:absolute;
    top:0;
    bottom:0;
    left:0;
    right:0;
    z-index:-1;
    transform:translateX(-120%);
    transition:transform 0.2s ease-out;
  }

  @media (hover: hover) {  //this means it will only occur on hover and not touch
    &:hover:after{transform:translateX(0);}
    &:hover .menustrips>div{background-color:$darkblue;}
  }
}

.project.br .menu_icon_container:after{background-color:#fff;}
.project.yukfoo .menu_icon_container:after{background-color:#fff;}



.menu_icon{
  position:relative;
  width:60px;
  height:54px;
  float:left;
}

.menu_icon.close-icon{
  .topstrip{background-color:$darkblue;transform:rotate(-45deg) translate(-7px,6px);}
  .midstrip{opacity:0;}
  .bottomstrip{background-color:$darkblue; transform:rotate(45deg) translate(-7px,-5px);}
}

.menustrips{
    position:absolute;
    margin:16px;
    &>div{
      width:30px;
      margin-top:6px;
      height:3px;
      background-color:#fff;
      transition:background-color 0.5s ease-out,transform 0.3s ease-out;
       &:first-child{
        margin-top:0;
      }
    }
  }


.menu_overlay.hidden{
  visibility: hidden;
  opacity: 0;
  // transition: visibility 0s linear 300ms, opacity 500ms;
}


