    }

    .wrapper.text{
      width:82%;
      max-width:885px;
    }




  .project_intro{
    @include fluid-type(padding-bottom, 20em, 70em, 6em, 8em);
    color:#fff;
    background-position:50% 0;
    background-size:100% auto;
    background-repeat:no-repeat;
    width:100%;
    position:relative;

    

    .background-blur_container{
      position:absolute;
      bottom:0;
      width:100%;
      height:100%;
      overflow:hidden;
    }

    .background-blur{
      position:absolute;
      bottom:0;
      width:100%;
      height:60%;
      filter: blur(4vw);
    }

    .project_title{
      font-family: 'Helvetica Neue Bold';
      border-top:2px solid #FFF;
      line-height:1.25;
      @include fluid-type(font-size, 20rem, 100rem, 1.333rem, 1.777rem);
      @include fluid-type(padding-top, 20em, 70em, 1em, 1em);
      @include fluid-type(padding-bottom, 20em, 70em, 0.5em, 0.5em);
      -webkit-font-smoothing: antialiased;
      @include bp(babybear){
        border-top:none;
        padding-top:0;
      }
    }


    
    .skills{
      line-height:2;
      text-transform:uppercase;
      @include fluid-type(font-size, 20rem, 100rem, 0.844rem, 0.844rem);
      @include fluid-type(padding-bottom, 20em, 70em, 3em, 2em);
      @include fluid-type(letter-spacing, 20em, 70em, 0.25em, 0.25em);
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;

    }

    h1{
      color:#fff;
      text-align:center;
      @include fluid-type(padding-top, 20em, 70em, 4em, 4em);
      @include fluid-type(padding-bottom, 20em, 70em, 3em, 3em);
      max-width:800px;
      width:90%;
      margin:0 auto;
      position:relative;
      z-index:1;
    }
  }

  .project_image{
    width:100%;
  }

}

.project_segments{
    z-index:1;
    @include fluid-type(padding-top, 20em, 70em, 4em, 4em);
    section{
      @include fluid-type(padding-bottom padding-top, 20em, 70em, 3em, 3em);
    }

    section.dbl_paddingtop{
      @include fluid-type(padding-top, 20em, 70em, 6em, 6em);
    }

    section.blue_back{
      background-image: url(../../dist/images/intropaper_kaha.jpg);
      background-size:cover;
      color:#fff;
    }

    section.lightgray_back{
      background-color:#E6E9EC;

    }
    section.no_padding_bottom{
      padding-bottom:0;
    }
}

.project_screen{
  width:100%;
  padding-top:2rem;
  margin-bottom:-3px;
}




.flexbox_container{
    h3{
      padding-top:0;
    }
      @include bp(babybear){
        display:flex;
        flex-wrap: wrap;
        justify-content: space-between;
        position: relative;
        width:100%;
        padding: 0;
        margin: 0;
        list-style: none;
      }
      .left_panel{
       z-index:1;
       position:relative;
      @include bp(babybear){
        width:33.33%;
      }
    }
    .right_panel{
       position:relative;
       z-index:1;
      h2{
        padding-top:0;
      }
      p{
        padding-top:0;
      }
      @include bp(babybear){
        width:56.52%;
      }
    }
}



